body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.poster {
    border: 1px solid black;
    position: relative;
    float: left;
    width:  222px;
    height: 328px;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: cover;
}

.items {
    margin-left: 50px;
    margin-right: 50px;
    margin-top: 25px;
}

.title {
    font-family: 'Open Sans';
    font-size: 1.05em;
    align-self: center;
}

.release {
    font-size: .8em;
}
.banner {
    display: flex;
    color: white;
  }

.pixar-logo {
    margin-left: 30px;
    margin-right: 40px;
    height: 120px;
    line-height: 120px;
    align-self: center;
    max-width:100%;
    max-height:100%;
  }

  .desc {
    color: whitesmoke;
    align-self: center;
    margin-left: 120px;
    font-size: 1.15em;
    font-family: 'Open Sans';
  }

  .luxo {
    height: 80px;
    line-height: 80px;
    align-self: center;
    margin-left: auto;
    margin-right: 10px;
  }

  .button1 {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 1em;
    align-self: center;
    background: #4D3431;
    color: white;
    border: 1.2px solid white;
  }
  
  .button1:hover {
    background: white;
    color:black;
  }

  .button1:focus {
      outline: 0;
  }
  
  .button2 {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: .9em;
    align-self: center;
    background: #4D3431;
    color:white;
    border: 1.2px solid white;
    margin-top: 1px;
  }
  
  .button2:hover {
    background: white;
    color:black;
  }
  
  .button2:focus {
    background: white;
    color:black;
    outline: 0;
  }
  
  .sort {
    align-self: center;
    margin-left: 15px;
    margin-right: 8px;
  }

  @media screen and (min-device-width : 320px) and (max-device-width : 480px) {
  .banner {
    width: 320px;
    flex-direction: column;
    align-items: center;
  }

  .pixar-logo {
    margin: 0;
  }

  .sort {
    margin: 0;
  }

  .luxo {
    align-self: center;
    margin: 0;
    margin-top: 20px;
    margin-bottom: 30px;
  }

  .desc {
    margin: 0;
    margin-top: 15px;
  }
}


@font-face {
  font-family: "Open Sans";
  src: url(/eric-elliott.github.io/static/media/OpenSans-Regular.d7d5d458.ttf);
}

html {
  background-image: linear-gradient(to right, #f4d194, #edbc83, #e5a674, #dd9169, #d37b60);
  background-size: contain;
}

.movies {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-around;
  align-items: center;
}

.footer {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 50px;
}

